@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orelega+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gelasio:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
:root {
  --darkBlack: #0d141f;
  --black: #282C34;
  --darkGrey: #4e5565;
  --grey: #6f7990;
  --lightGrey: #C5C5C5;
  --white: #ececec;
  --lightWhite: #ffffff;
  --darkerOrange: #d45231;
  --darkOrange: #e07155;
  --lightOrange: rgb(221, 122, 97);
  --placerholderColorLight: #a3a3a3;
  --placerholderColorDark: grey;
  --headerFont: 'Gelasio', serif;
  --textFont: 'Gelasio', serif;
  --audioIconBlack: url(data:image/png;base64,);
  --audioIconWhite: url(data:image/png;base64,);
  --searchIconBlack: url(data:image/png;base64,);
  --searchIconWhite: url(data:image/png;base64,);
  --arrowDownBlack: url(data:image/png;base64,);
  --arrowDownWhite: url(data:image/png;base64,);
  --fishLoaderBlack: url(/WordOcean/static/media/loader_fish_black.e696368e.svg);
  --fishLoaderWhite: url(/WordOcean/static/media/loader_fish_white.4b1fc787.svg);
  --githubBlack: url(data:image/png;base64,);
  --githubWhite: url(data:image/png;base64,);
  --puddleWhite: url(data:image/png;base64,);
  --puddleBlack: url(data:image/png;base64,);
  --standardTransition: all 0.3s ease-in-out;
  --inputBorder: 1px solid hsl(0, 0%, 80%);
  --outputBorderGradientLight: linear-gradient(90deg, rgba(40,44,52,0) 0%, rgba(40,44,52,0) 30%, rgba(40,44,52,1) 100%);
  --outputBorderGradientDark: linear-gradient(90deg, rgba(236,236,236,0) 0%, rgba(236,236,236,0) 30%, rgba(236,236,236,1) 100%);
}

* {
  box-sizing: border-box;
}

body {
  background: var(--background-color);
  color: var(--text-color);
  transition: all 0.3s ease-in-out;
  transition: var(--standardTransition);
  font-family: 'Gelasio', serif;
  font-family: var(--textFont);
  box-sizing: border-box;
}

body.light {
  --background-color: var(--white);
  --input-bg-color: var(--white);
  --text-color: var(--black);
  --input-text-color: var(--black);
  --input-border: var(--inputBorder);
  --background-accent-color: var(--lightOrange);
  --text-accent-color: var(--darkerOrange);
  --hover-color: var(--lightGrey);
  --audio-icon: var(--audioIconBlack);
  --search-icon: var(--searchIconBlack);
  --arrow-down-icon: var(--arrowDownBlack);
  --select-scrollbar: var(--lightWhite);
  --select-scrollbar-thumb: var(--lightGrey);
  --loading-gif: var(--fishLoaderBlack);
  --output-border: var(--black);
  --output-gradient-border: var(--outputBorderGradientLight);
  --placeholder-color: var(--placerholderColorLight);
  --github-logo: var(--githubBlack);
  --puddle-icon: var(--puddleBlack);
}

body.dark {
  --background-color: var(--black);
  --input-bg-color: var(--black);
  --text-color: var(--white);
  --input-text-color: var(--white);
  --input-border: var(--inputBorder);
  --background-accent-color: var(--darkerOrange);
  --text-accent-color: var(--lightOrange);
  --hover-color: var(--darkGrey);
  --audio-icon: var(--audioIconWhite);
  --search-icon: var(--searchIconWhite);
  --arrow-down-icon: var(--arrowDownWhite);
  --select-scrollbar: var(--darkBlack);
  --select-scrollbar-thumb: var(--darkGrey);
  --loading-gif: var(--fishLoaderWhite);
  --output-border: var(--white);
  --output-gradient-border: var(--outputBorderGradientDark);
  --placeholder-color: var(--placerholderColorDark);
  --github-logo: var(--githubWhite);
  --puddle-icon: var(--puddleWhite);
}

.App {
  padding: 0 5vw;
}

@media only screen and (max-width: 900px){
  .App {
    padding: 0;
  }
}

.header {
    padding: 1.5em 0 1em;
    position: relative;
}

.header_github {
    background: transparent var(--github-logo) no-repeat center;
    background-size: 30px 30px;
    width: 45px;
    height: 45px;
    border: none;
    transition: var(--standardTransition);
    position: absolute;
    top: 5vh;
    left: 3vw;
}

.header_github:hover {
    cursor: pointer;
}

.header_text {
    font-size: 5em;
    font-weight: 700;
    font-family: var(--headerFont);
    line-height: 1;
    text-align: center;
    margin: 0 15%;
}

#darkModeToggle {
    position: absolute;
    top: 5vh;
    right: 3vw;
}

@media only screen and (max-width: 900px) {
    .header_text {
        font-size: 4em;
    }
}

@media only screen and (max-width: 350px){
    .header_text {
        font-size: 3em;
    }
  }
  
#darkModeToggle {
    background: transparent url(/WordOcean/static/media/moon.e6fcfcd3.png) no-repeat center;
    background-size: 30px 30px;
    width: 45px;
    height: 45px;
    border: none;
    border-radius: 50%;
    transition: var(--standardTransition);
  }

  #darkModeToggle:focus {
    border: none;
  }
  
  #darkModeToggle:hover {
    background-color: var(--black);
    cursor: pointer;
  }
  
  #darkModeToggle.clicked {
    background-image: url(/WordOcean/static/media/sun.b6776e11.png);
    background-color: transparent;
  }

  #darkModeToggle.clicked:hover {
      background-color: var(--white);
  }
.input {
    margin-top: 15vh;
    height: 20vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.word_input_container {
    display: -webkit-flex;
    display: flex; 
    -webkit-flex-direction: column; 
            flex-direction: column;
}

.input_word, .search_container {
    background-color: var(--input-bg-color);
    color: var(--input-text-color);
    height: 50px;
    font-size: 1.2rem;
    font-family: var(--textFont);
    font-weight: 500;
    border-radius: 4px;
    border: var(--input-border);
    outline: 0;
    padding: 2px 8px;
    max-width: calc(100% - 8px);
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.input_word {
    width: 300px;
    transition-property: background-color, color, border;
    transition-duration: 0.3s;
    transition-timing-function: ease-in-out;
}

.search_container, .input_search {
    cursor: pointer;
    transition-duration: 0.3s;
    transition-timing-function: ease-in-out;
    transition-property: background-color, color;
}

.search_container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    background-color: var(--input-bg-color);
    width: 115px;
}

.search_container:hover {
    background-color: var(--hover-color);
}

.search_container:hover .input_search {
    background-color: var(--hover-color);
}

.input_search {
    background-color: var(--input-bg-color);
    font-size: 1.2rem;
    border: none;
    font-family: var(--textFont);
    font-weight: 500;
    color: var(--input-text-color);
    padding-left: 0;
}

.search_icon {
    background: var(--search-icon);
    height: 25px;
    width: 25px;
    transition: all 0.3s ease-in-out;
}

::-webkit-input-placeholder {
    color: var(--placeholder-color);
    opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
    color: var(--placeholder-color);
    opacity: 1; /* Firefox */
}

::placeholder {
    color: var(--placeholder-color);
    opacity: 1; /* Firefox */
}

::-ms-input-placeholder {
    color: var(--placeholder-color);
}

@media only screen and (max-width: 900px){
    .input {
        margin-top: 5vh;
        margin-bottom: 7vh;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: center;
                align-items: center;
    }

    .input_word {
        width: 310px;
    }

    .search_container {
        width: 310px;
        -webkit-justify-content: center;
