.input_hint {
    height: 20px;
    color: red;
}
.container {
  margin-right: 0.5rem;
}

.select-box, .options-container, .selected {
  transition-property: background-color, color, background;
  transition-duration: 0.3s;
  transition-timing-function: ease-in-out;
}

.select-box {
  display: -webkit-flex;
  display: flex;
  width: 300px;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: relative;
}

.select-box .options-container {
  background: var(--input-bg-color);
  color: var(--input-text-color);
  max-height: 0;
  opacity: 0;
  border-radius: 8px;
  overflow: hidden;
  
  -webkit-order: 1;
  
          order: 1;
}

.selected {
  background: var(--input-bg-color);
  border-radius: 4px;
  margin-bottom: 8px;
  color: var(--input-text-color);
  position: relative;
  border: var(--input-border);
  -webkit-order: 0;
          order: 0;
  height: 50px;
}

.nothing-selected {
  color: var(--placeholder-color);
}

.selected::after {
  content: "";
  background: var(--arrow-down-icon);
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  height: 20px;
  width: 20px;
  right: 16px;
  top: 14px;
  transition: var(--standardTransition);
}

.options-container {
  position: absolute;
  left: -1px;
  top: 59px;
  width: 302px;
}

.options-container.active {
  border: var(--input-border);
}

.select-box .options-container.active {
  max-height: 240px;
  opacity: 1;
  overflow-y: scroll;
  z-index: 1;
}

.select-box .options-container.active + .selected::after {
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
  top: 14px;
}

.select-box .options-container::-webkit-scrollbar {
  width: 8px;
  background-color: var(--select-scrollbar);
  border-radius: 0 8px 8px 0;
  -webkit-transition: background-color 0.3s ease-in-out;
  transition: background-color 0.3s ease-in-out;
}

.select-box .options-container::-webkit-scrollbar-thumb {
  background-color: var(--select-scrollbar-thumb);
  border-radius: 0 8px 8px 0;
  -webkit-transition: background-color 0.3s ease-in-out;
  transition: background-color 0.3s ease-in-out;
}

.select-box .option,
.selected {
  padding: 12px 8px;
  cursor: pointer;
  font-size: 1.2rem;
}

.select-box .option:hover {
  background: var(--hover-color);
}

.select-box label {
  cursor: pointer;
}

.select-box .option .radio {
  display: none;
}

@media only screen and (max-width: 350px){
  .select-box {
    width: 242px;
  }

  .options-container {
    width: 244px;
  }
}

.output {
    font-family: var(--textFont);
}

.output_header {
    display: grid;
    padding: 0 40px 40px 40px;
    grid-template-columns: 60% 40%;
    grid-gap: 10px;
    gap: 10px;
    grid-template-areas: 
    "word phonetics"
    "lang .";
    border-bottom: 1px solid var(--output-border);
}

.output_word {
    font-size: 80px;
    font-weight: 600;
    line-height: 1.2;
    grid-area: word;
    align-self: center;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}


.output_lang {
    font-size: 25px;
    grid-area: lang;
}

.output_lang_text {
    background-color: var(--background-accent-color);
    color: var(--text-color);
    padding: 5px 10px;
    border-radius: 5px;
    transition-property: background-color, color;
    transition-duration: 0.3s;
    transition-timing-function: ease-in-out;
}

.output_phonetics {
    font-size: 20px;
    grid-area: phonetics;
    align-self: center;
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.output_phonetics > div {
    margin-right: 1rem;
}

.output_phonetics-pair {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    border: 1px solid var(--background-accent-color);
    padding: 0.35rem 1.2rem 0.35rem 1.5rem;
    border-radius: 20px;
    margin-bottom: 6px;
}

.output_meanings {
    margin-bottom: 25px;
}

@media only screen and (max-width: 1100px){
    .output_phonetics > div {
        margin-right: 0.5rem;
    }

    .output_phonetics {
        font-size: 16px;
    }
}

@media only screen and (max-width: 900px){
    .output_header {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        grid-gap: 10px;
        gap: 10px;
        border-bottom: 1px solid var(--output-border);
    }

    .output_word {
        font-size: 50px;
        font-weight: 600;
        line-height: 1;
        grid-area: word;
        align-self:flex-start;
    }

    .output_lang {
        font-size: 18px;
        grid-area: lang;
    }

    .output_phonetics {
        -webkit-justify-content: flex-start;
                justify-content: flex-start;
        margin-top: 10px;
    }
}
.audioIcon {
    height: 25px;
    width: 25px;
    margin-left: 0.5rem;
    color: white;
    background: var(--audio-icon);
    transition: background 0.3s ease-in-out;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
}

.audioIcon:hover {
    cursor: pointer;
}

@media only screen and (max-width: 1100px){
    .audioIcon {
        height: 22px;
        width: 22px;
        margin-bottom: 1px;
    }
}

@media only screen and (max-width: 900px){
    .audioIcon {
        margin-bottom: 2px;
        margin-left: 0.25rem;
    }
}
.word_list_container {
    display: -webkit-flex;
    display: flex;
}

.word_list_words {
    list-style: none;
    margin: 0;
    padding-left: 1.5rem;
}

.word_list_item {
    display: inline;
    padding-right: 5px;
    padding-bottom: 5px;
    /* Makes the list elements wrap into the next row */
    float: left;
    color: var(--text-accent-color);
    transition: color 0.3s ease-in-out;
}

.word_list_item::after {
    content: ", ";
    color: var(--text-color);
    transition: color 0.3s ease-in-out;
}

.word_list_item:last-child::after {
    content: "";
}
.meaning {
  display: -webkit-flex;
  display: flex;
  min-height: 200px;
}

.part_of_speech {
  -webkit-flex: 0 0 20%;
          flex: 0 0 20%;
  border-right: 1px solid var(--output-border);
  border-bottom: 1px solid transparent;
  border-image: var(--output-gradient-border);
  border-image-slice: 1;
  text-align: right;
  padding-right: 50px;
  font-weight: 500;
  padding-top: 2rem;
}

.part_of_speech.last_one {
  border-bottom: none;
}

.part_of_speech_text {
  border-bottom: 1px solid var(--text-accent-color);
}

.definitions {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%
}

.definition {
  font-weight: 500;
